<template>
  <div>
        <v-select
          class=""
          :items="inOptions"
          menu-props="auto"
          :label="label"
          @input="onInputChange"
          v-model="inputValue"
          prepend-icon=""
        >
        </v-select>
  </div>
</template>


<script>
//import {eventBus} from "../main";

  export default {
    data: function(){
      return{
        inputValue: this.value
      }
    },
    props: {
    value:{},
      label: {
        type: String,
        // adding more attributes to a property
        required: true,
        default: " ",
      },
      inOptions: {
        type: Array,
        // adding more attributes to a property
        required: true,
      },
      required: Boolean,
      type: String,
    },
    methods: {
      onInputChange(){
        this.$emit('input',this.inputValue);
      }
    }
  }
</script>


<style scoped>
/* Scoped styles are "component based" scope */
</style>>
