<template>
  <v-card class="mt-5 mx-auto rounded-lg elevation-4"
    :style="{marginBottom: '150px'}"
    min-width="50vw" max-width="95vw">
    <v-form ref="form" v-model="valid" v-if="!postSent">
      <v-container fluid>
        <!-- Registro title -->
        <div class="my-6">
          <p class="display-2 blue-grey--text text--darken-3 font-weight-medium text-center">{{this.tour.title}}</p>
        </div>
        <div class="my-2">
          <p class="display-1 blue-grey--text text--darken-3 font-weight-medium text-center">Registro</p>
        </div>

        <!-- Asking for required fields from incomming data -->
        <div v-if="this.tour.request_first_name">
          <t-input  :label="'Nombre'" :type="'String'" :rule="rule1" v-model="formData.name"></t-input>
        </div>
        <div v-if="this.tour.request_last_name">
          <t-input  :label="'Apellido'" :type="'String'" :rule="rule1" v-model="formData.lastName"></t-input>
        </div>
        <div v-if="this.tour.request_email">
          <t-input  :label="'Email'" :type="'String'" :rule="ruleEmail" v-model="formData.email"></t-input>
        </div>
        <div v-if="this.tour.request_sex">
            <!-- Enviar opciones aca -->
            <t-select :label="'Sexo'" :inOptions="sexOptions" v-model="formData.sex"></t-select>
        </div>
        <div v-if="this.tour.request_nationality">
            <!-- Enviar opciones aca -->
            <t-select :label="'Nacionalidad'" :inOptions="nationalityOptions" v-model="formData.nationality"></t-select>
        </div>
        <div v-if="this.tour.request_dpi_passport">
          <t-input  :label="'DPI/Pasaporte'" :type="'String'" :rule="ruleDPI" v-model="formData.dpiPass"></t-input>
        </div>
        <div v-if="this.tour.request_phone">
          <t-input  :label="'Telfono celular'" :type="'String'" :rule="rulePhone" v-model="formData.phone"></t-input>
        </div>
        <div v-if="this.tour.request_ocupation">
          <t-input  :label="'Ocupacin'" :type="'String'" :rule="rule2" v-model="formData.occupation"></t-input>
        </div>
        <!-- inscripcion title -->
        <div class="mt-10">
          <p class="display-2 blue-grey--text text--darken-3 font-weight-medium text-center">Inscripcin</p>
          <p class="subheading  blue-grey--text text--darken-4 text-center">Llena tus datos para crear una cuenta en FIE</p>
        </div>
        <!-- Contacto de emergencia sub title -->
        <div v-if="this.tour.request_emergencyContact || this.tour.request_emergencyContact_num">
          <div class="mt-5">
            <p class="title blue-grey--text text--darken-3 font-weight-medium text-left">Contacto de emergencia</p>
          </div>
          <div >
            <t-input  :label="'Nombre padre/encargado'" :type="'String'" :rule="rule2" v-model="formData.encargadoName"></t-input>
          </div>
          <div >
            <t-input  :label="'Celular padre/encargado'" :type="'String'" :rule="rulePhone2" v-model="formData.encargadoPhone"></t-input>
          </div>
          <div >
            <t-input  :label="'Correo Electrnico padre/encargado'" :type="'String'" :rule="ruleEmail2" v-model="formData.encargadoEmail"></t-input>
          </div>
          <div >
            <t-input  :label="'Nombre contacto de emergencia'" :type="'String'" :rule="rule1" v-model="formData.emergencyName"></t-input>
          </div>
          <div v-if="this.tour.request_emergencyContact_num">
            <t-input  :label="'Celular contacto de emergencia'" :type="'String'" :rule="rule1" v-model="formData.emergencyPhone"></t-input>
          </div>
        </div>
        
        <!-- Contacto de referencia -->
        <div class="mt-5">
          <p class="title blue-grey--text text--darken-3 font-weight-medium text-left">Contacto de referencia</p>
        </div>
        <div >
          <t-input  :label="'Nombre contacto de referencia'" :type="'String'" :rule="rule1" v-model="formData.referenceName"></t-input>
        </div>
        <div >
          <t-input  :label="'Celular contacto de referencia'" :type="'String'" :rule="rule1" v-model="formData.referencePhone"></t-input>
        </div>
        <!-- Informacin especifica -->
        <div class="mt-5">
          <p class="title blue-grey--text text--darken-3 font-weight-medium text-left">Informacin especifica</p>
        </div>
        <!--  -->
        <div v-if="this.tour.request_medAlergy">
          <div>
          <t-select :label="'Alerga a algn medicamento?'" :inOptions="boolOptions" v-model="formData.alergyBool"></t-select>
          </div>
          <div class="mb-6">
            <t-input  
              :label="'Especfica'" :type="'String'" :rule="rule2" v-model="formData.alergySpec"
              :disabled="!(formData.alergyBool === 'Si')"
              ></t-input>
          </div>
        </div>
        <!--  -->
        <div v-if="this.tour.request_foodAlergy">
          <div>
            <t-select :label="'Alerga a algn alimento?'" :inOptions="boolOptions" v-model="formData.foodAlergyBool"></t-select>
          </div>
          <div class="mb-6">
            <t-input
              :label="'Especfica'" :type="'String'" :rule="rule2" v-model="formData.foodAlergySpec"
              :disabled="!(formData.foodAlergyBool === 'Si')"
            ></t-input>
          </div>
        </div>
        <!--  -->
        <div v-if="this.tour.request_injury">
          <div>
            <t-select :label="'Alguna lesin o capacidad especial?'" :inOptions="boolOptions" v-model="formData.lesionBool"></t-select>
          </div>
          <div class="mb-6">
            <t-input
              :label="'Especfica'" :type="'String'" :rule="rule2" v-model="formData.lesionSpec"
              :disabled="!(formData.lesionBool === 'Si')"
              ></t-input>
          </div>
        </div>
        <!--  -->
        <div v-if="this.tour.request_diet">
          <div class="mb-3">
              <t-select :label="'Alguna dieta especial?'" :inOptions="dietOptions" v-model="formData.dietSpec"></t-select>
          </div>
        </div>
        <div v-if="this.tour.request_bloodType">
          <t-select :label="'Tipo de sangre?'" :inOptions="bloodOptions" v-model="formData.bloodType"></t-select>
        </div>
        <div v-if="this.tour.request_park">
          <t-select :label="'A qu parque de diversiones te gustara asistir?'" :inOptions="irtraOptions" v-model="formData.parkSel"></t-select>
        </div>
        <div v-if="this.tour.request_transport1">
          <t-select :label="'Utilizars el transporte de ida?'" :inOptions="boolOptions" v-model="formData.transport1Bool"></t-select>
        </div>
        <div v-if="this.tour.request_transport2">
          <t-select :label="'Utilizars el transporte de regreso?'" :inOptions="boolOptions" v-model="formData.transport2Bool"></t-select>
        </div>
        <!-- Pago -->
        <div class="mt-10">
          <p class="display-2 blue-grey--text text--darken-3 font-weight-medium text-center">Pago</p>
          <p class="subheading  blue-grey--text text--darken-4 text-center">Llena tus datos para pagar tu Inscripcin</p>
        </div>
        <!-- Informacin de pago -->
        <div class="mt-5">
          <p class="title blue-grey--text text--darken-3 font-weight-medium text-left">Informacin de Pago</p>
        </div>
        <div class="mb-2">
          <t-input  :label="'Nombre para el recibo'" :type="'String'" :rule="rule2" v-model="formData.receiptName"></t-input>
        </div>
        <div class="mb-2">
          <t-input  :label="'NIT para el recibo'" :type="'String'" :rule="rule2" v-model="formData.receiptNum"></t-input>
        </div>
        <hr-divider class="mt-10" />
        <!-- total y descuento -->
        <div>
            <v-row>
              <v-col>
              <p :style="{fontSize: '30px'}" class="blue-grey--text text--darken-3 font-weight-medium text-left">Sub-total</p>
              </v-col>
              <v-col>
                <p :style="{fontSize: '30px'}" class="blue-grey--text text--darken-3 font-weight-medium text-right">Q{{this.tour.category[0].prices[0].price}}</p>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <p class="subheading  blue-grey--text text--darken-4 text-left">Posees un cdigo de descuento?</p>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
              <div class="">
                <t-input  :label="'Codigo'" :type="'String'" :rule="rule2" v-model="formData.discountCode"></t-input>
              </div>
              </v-col>
              <v-col class="text-right mr-3">
                <v-btn class="" @click="validateDiscount(formData.discountCode)" >Aplicar</v-btn>
              </v-col>
            </v-row>
            <v-row class="d-flex justify-center">
              <v-snackbar
                v-model="validDiscount"
                :timeout="3000"
              >
                Descuento del {{totalDiscount}}% aplicado xitosamente!

                <template v-slot:action="{ attrs }">
                  <v-btn
                    color="blue"
                    text
                    v-bind="attrs"
                    @click="snackbar = false"
                  >
                    Close
                  </v-btn>
                </template>
              </v-snackbar>
              <v-snackbar
                v-model="invalidDiscount"
                :timeout="3000"
              >
                El cdigo de descuento: {{formData.discountCode}} no es vlido.

                <template v-slot:action="{ attrs }">
                  <v-btn
                    color="blue"
                    text
                    v-bind="attrs"
                    @click="snackbar = false"
                  >
                    Close
                  </v-btn>
                </template>
              </v-snackbar>
              
            </v-row>
            <v-row class="mt-5">
              <v-col>
              <p  :style="{fontSize: '20px'}" class="subheading blue-grey--text text--darken-3 font-weight-medium text-left">Descuento</p>
              </v-col>
              <v-col>
                <p  :style="{fontSize: '20px'}" class="subheading blue-grey--text text--darken-3 font-weight-medium text-right mr-5">{{totalDiscount}}%</p>
              </v-col>
            </v-row>
            <hr-divider class="" />
            <v-row>
              <v-col>
              <p :style="{fontSize: '30px'}" class="blue-grey--text text--darken-3 font-weight-medium text-left">Total</p>
              </v-col>
              <v-col>
                <p :style="{fontSize: '30px'}" class="blue-grey--text text--darken-3 font-weight-medium text-right">Q{{(this.tour.category[0].prices[0].price)*(1-(this.totalDiscount/100))}}</p>
              </v-col>
            </v-row>
        </div>
        <!-- Payment methods  -->
        <div>
          <div class="mt-5">
            <p :style="{fontSize: '32px'}" class="blue-grey--text text--darken-3 font-weight-medium text-left">Mtodo de pago</p>
          </div>
          <v-row>
            <v-col class="text-center">
                <btns-group :selectedMethod="selectedMethod" :paymentType="paymentType"  @paymentTypeWasChosen="paymentType = $event" @paymentMethodWasChosen="selectedMethod = $event" />
            </v-col>
          </v-row>

          <div>
            <keep-alive>
              <component :is="selectedMethod" class="my-4"

                :input-card-number="formData.cardNumber" :input-card-name="formData.cardName"
                :input-card-month="formData.cardMonth" :input-card-year="formData.cardYear"
                :input-card-cvv="formData.cardCvv"

                @input-card-number="formData.cardNumber = $event"
                @input-card-name="formData.cardName = $event"
                @input-card-month="formData.cardMonth = $event"
                @input-card-year="formData.cardYear = $event"
                @input-card-cvv="formData.cardCvv = $event"

                :receiptNum="formData.receipt"
                :bankName="formData.bank"
                :fileData="formData.file"
                @boletaWasChanged="formData.receipt = $event"
                @bancoWasChanged="formData.bank = $event"
                @fileWasChanged="formData.file = $event"

              >
                <!-- This component chooses dinamically between components :) - magically -->
                <h4>Selecciona un mtodo de pago</h4>
              </component>
            </keep-alive>
          </div>
        
        </div>
      </v-container>
      
      <!-- POST BTN -->
      <v-container class="text-center mb-10">
        <v-row class="mt-10">
          <v-col class="d-flex justify-center">
            <v-checkbox class="" color="indigo darken-3" v-model="formData.termsCond" :label="`Acepto los trminos y condiciones FIE 2020`"></v-checkbox>
          </v-col>
        </v-row>
        <v-row class="d-flex justify-center">
          <v-col class="col-7" v-if="formData.termsCond && !showLoading && !postSent">
            <v-btn
              class="" block large color="indigo darken-3 white--text"
              @click="validate" :style="{minWidth: '150px'}"
              >
                Registrarme
              </v-btn>
          </v-col>
        </v-row>
        <v-row class="d-flex justify-center" v-if="showLoading">
          <v-col>
            <v-progress-circular
              indeterminate
              color="primary"
            ></v-progress-circular>
          </v-col>
        </v-row>
      </v-container>
    </v-form>
    <div v-if="postSent">
      <v-col>
        <v-row class="d-flex justify-center my-5" v-if="postSuccess">
          <v-chip
            class="ma-2"
            
            color="primary"
            text-color="white"
          >
            <v-avatar left>
              <v-icon>mdi-checkbox-marked-circle</v-icon>
            </v-avatar>
            Transaccin xitosa!
          </v-chip>
        </v-row>
        <v-row class="d-flex justify-center my-5" v-if="!postSuccess">
          <v-chip
            class="ma-2"
            
            color="red"
            text-color="white"
          >
            <v-avatar left>
              <v-icon>mdi-checkbox-marked-circle</v-icon>
            </v-avatar>
            Transaccin Errnea!
          </v-chip>
        </v-row>
      </v-col>
    </div>
  </v-card>
</template>

<script>
import Input from "./Input.vue";
import Select from "./Select.vue";
import demoData from "../demoData/demo1.json";
import HrDivider from "./HrDivider.vue";
import BtnsGroup from "./BtnsGroup.vue";
import CardForm from "./payment-components/CardForm.vue";
import DepositForm from "./payment-components/DepositForm.vue";
import Bipagos from "./payment-components/BiPagosForm.vue";

//import {eventBus} from "../main";


console.log(demoData);
export default {
  data: () => {
    return {
      valid: true,
      name1: "Nombre Completo",
      type: "text",
      showLoading: false,
      postSent: false,
      postSuccess: null,
      // input fields data
      formData: {
        name: '',
        lastName: '',
        email: '',
        sex: '',
        nationality: '',
        dpiPass: '',
        phone: '',
        occupation: '',
        encargadoName: '',
        encargadoPhone: '',
        encargadoEmail: '',
        emergencyName: '',
        emergencyPhone: '',
        referenceName: '',
        referencePhone: '',
        alergyBool: '',
        alergySpec: '',
        foodAlergyBool: '',
        foodAlergySpec: '',
        lesionBool: '',
        lesionSpec: '',
        dietBool: '',
        dietSpec: '',
        bloodType: '',
        parkSel: '',
        transport1Bool: '',
        transport2Bool: '',
        receiptName: '',
        receiptNum: '',
        total: 0,
        discount: 0,
        // --- payment method fields
        alias: 'Credit Card',
        cardName: '',
        cardNumber: '',
        cardMonth: '',
        cardYear: '',
        cardCvv: '',
        fingerprint: '',
        // deposit
        receipt: '',
        bank: '',
        file: '',
        termsCond: false,
        // --- utm source info
        utmMedium: '',
        utmSource: '',
        // ip address
        shopperIp: '',

      },
      this: demoData,
      // tour space
      tour: null,
      tour_id: null,
      // ---- Discount ---
      validDiscount: false,
      invalidDiscount: false,
      totalDiscount: 0,
      // -----------------

      selectedMethod: 'card-form',
      paymentType: 'epay',
      // --- rules ---
      rule1: [
      value => !!value || "Requerido",
      ],
      rule2: [
      
      ],
      ruleEmail: [ 
        value => !!value || "Requerido",
        value => !value || /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(value) || 'El correo no es vlido'
      ],
      ruleEmail2: [ 
        //value => !!value || "Requerido",
        value => !value || /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(value) || 'El correo no es vlido'
      ],
      ruleDPI: [
      value => !!value || "Requerido",
      value => (value && value.length >= 7) || 'El DPI o Pasaporte debe tener al menos 7 carcteres.'
      ],
      rulePhone: [
      value => !!value || "Requerido",
      value => (value && value.length >= 8) || 'El telfono debe tener al menos 8 carcteres.'
      ],
      rulePhone2: [
      // value => !!value || "Requerido",
      // value => (value && value.length >= 8) || 'El telfono debe tener al menos 8 carcteres.'
      ],
      // --- rules end ---
      // --- select options ---
      dietOptions: [
        'Ninguna en especfico', 'Vegana' , 'Vegetariana', 'Kosher'
      ],
      sexOptions: [
            'Masculino', 'Femenino', 'Otro', 
      ],
      boolOptions: [
        'Si', 'No',
      ],
      bloodOptions: [
        'O-','O+','A-','A+','B-','B+','AB-','AB+'
      ],
      irtraOptions: [
        'Xetulul', 'Xocomil','Xejuyup'
      ],
      nationalityOptions: [
        "Afganistn","Albania","Alemania","Andorra","Angola","Antigua y Barbuda","Arabia Saudita","Argelia",
        "Argentina","Armenia","Australia","Austria","Azerbaiyn","Bahamas","Banglads","Barbados","Barin",
        "Blgica","Belice","Benn","Bielorrusia","Birmania","Bolivia","Bosnia y Herzegovina","Botsuana",
        "Brasil","Bruni","Bulgaria","Burkina Faso","Burundi","Butn","Cabo Verde","Camboya","Camern",
        "Canad","Catar","Chad","Chile","China","Chipre","Ciudad del Vaticano","Colombia","Comoras",
        "Corea del Norte","Corea del Sur","Costa de Marfil","Costa Rica","Croacia","Cuba","Dinamarca",
        "Dominica","Ecuador","Egipto","El Salvador","Emiratos rabes Unidos","Eritrea","Eslovaquia",
        "Eslovenia","Espaa","Estados Unidos","Estonia","Etiopa","Filipinas","Finlandia","Fiyi","Francia",
        "Gabn","Gambia","Georgia","Ghana","Granada","Grecia","Guatemala","Guyana","Guinea","Guinea ecuatorial",
        "Guinea-Bisu","Hait","Honduras","Hungra","India","Indonesia","Irak","Irn","Irlanda","Islandia",
        "Islas Marshall","Islas Salomn","Israel","Italia","Jamaica","Japn","Jordania","Kazajistn","Kenia",
        "Kirguistn","Kiribati","Kuwait","Laos","Lesoto","Letonia","Lbano","Liberia","Libia","Liechtenstein",
        "Lituania","Luxemburgo","Madagascar","Malasia","Malaui","Maldivas","Mal","Malta","Marruecos","Mauricio",
        "Mauritania","Mxico","Micronesia","Moldavia","Mnaco","Mongolia","Montenegro","Mozambique","Namibia",
        "Nauru","Nepal","Nicaragua","Nger","Nigeria","Noruega","Nueva Zelanda","Omn","Pases Bajos","Pakistn",
        "Palaos","Panam","Papa Nueva Guinea","Paraguay","Per","Polonia","Portugal","Reino Unido",
        "Repblica Centroafricana","Repblica Checa","Repblica de Macedonia","Repblica del Congo",
        "Repblica Democrtica del Congo","Repblica Dominicana","Repblica Sudafricana","Ruanda","Rumana",
        "Rusia","Samoa","San Cristbal y Nieves","San Marino","San Vicente y las Granadinas","Santa Luca",
        "Santo Tom y Prncipe","Senegal","Serbia","Seychelles","Sierra Leona","Singapur","Siria","Somalia",
        "Sri Lanka","Suazilandia","Sudn","Sudn del Sur","Suecia","Suiza","Surinam","Tailandia","Tanzania",
        "Tayikistn","Timor Oriental","Togo","Tonga","Trinidad y Tobago","Tnez","Turkmenistn","Turqua","Tuvalu",
        "Ucrania", "Uganda","Uruguay","Uzbekistn","Vanuatu","Venezuela","Vietnam","Yemen","Yibuti","Zambia","Zimbabue"
      ],
      // ---- select options end ---
    };
  },
  props:{
    
  },
  components: {
    "t-input": Input,
    "t-select": Select,
    "hr-divider": HrDivider,
    "btns-group": BtnsGroup,
    "card-form": CardForm,
    "deposit-form": DepositForm,
    "bipagos-form": Bipagos,
  },
  methods: {
    validate(){
      this.$refs.form.validate();
      if(this.valid){
        console.log('Es valido!');
        this.formSubmit();
      } else {
        console.log('Es invalido');
      }
    },
    formSubmit(){
        this.showLoading = true;
        console.log('Data to submit:', this.formData);
        this.$http.post('https://fie.mayfer.dev/api/v1/tours/'+this.tour_id+'/book_and_pay/web', {
            tour_id: this.tour_id,
            [this.tour.category[0].prices[0].id]: 1,
            first_name: this.formData.name,
            last_name: this.formData.lastName,
            email: this.formData.email,
            sex: this.formData.sex,
            nationality: this.formData.nationality,
            dpi_passport: this.formData.dpi_passport,
            phone: this.formData.phone,
            occupation: this.formData.occupation,
            encargado_name: this.formData.encargadoName,
            encargado_phone: this.formData.encargadoPhone,
            emergency_name: this.formData.emergencyName,
            emergency_phone: this.formData.emergencyPhone,
            reference_name: this.formData.referenceName,
            reference_phone: this.formData.referencePhone,
            alergy_bool: this.formData.alergyBool,
            alergy_spec: this.formData.alergySpec,
            food_alergy_bool: this.formData.foodAlergyBool,
            food_alergy_spec: this.formData.foodAlergySpec,
            lesion_bool: this.formData.lesionBool,
            lesion_spec: this.formData.lesionSpec,
            diet_bool: this.formData.dietBool,
            diet_spec: this.formData.dietSpec,
            blood_type: this.formData.bloodType,
            park_sel: this.formData.parkSel,
            transport1_bool: this.formData.transport1Bool,
            transport2_bool: this.formData.transport2Bool,
            nit_name: this.formData.receiptName,
            nit: this.formData.receiptNum,
            shopper_ip: this.formData.shopperIp,
            
            // totals
            total_discount: this.formData.totalDiscount,
            total_amount: this.tour.category[0].prices[0].price,
            // -- payment
            payment_type: this.paymentType,
            // card
            credit_card_cvv: this.formData.cardCvv,
            credit_card_expiry: String(this.formData.cardYear).substring(2,4)+String(this.formData.cardMonth),
            credit_card_name: this.formData.cardName,
            credit_card_number: this.formData.cardNumber,
            fingerprint: this.formData.fingerprint,
            
            // deposit
            receipt: this.formData.receipt,
            bank: this.formData.bank,
            file: this.formData.file,
            // --
            utm_medium: this.formData.utmMedium,
            utm_source: this.formData.utmSource,
        })
        .then((response) => {
          console.log(response);
          if(response.data.code == "epay_success" && response.data.status == 200){
            this.postSuccess = true;
            this.showLoading = false;
            this.postSent = true;
          } else {
            this.postSuccess = false;
            this.showLoading = false;
            this.postSent = true;
          }
          
        })
        .catch((error) => {
            console.log(error);
            this.postSuccess = false;
            this.showLoading = false;
            this.postSent = true;
        });
    },
    validateDiscount(code){
      console.log(code);
      let myObj = this.tour.discount_codes;

      Object.keys(myObj).forEach(key => {
        if(code === myObj[key].codigo){
          this.validDiscount = true;
          this.totalDiscount = parseInt(myObj[key].percentage);
        } else {
          this.invalidDiscount = true;
          this.totalDiscount = 0;
        }
      });
    },
