<template class="">
  <div>
  <div class="ml-8">

    <v-row>
      <v-col>
        <p :style="{fontSize: '25px'}" class="blue-grey--text text--darken-3 font-weight-medium text-center">Carga el comprobante</p>
        <p :style="{fontSize: '15px'}" class="blue-grey--text text--darken-3 font-weight-light text-center">Adjunta un archivo png, jpg o jpeg menor a 5 MB</p>
      </v-col>
    </v-row>
  
    <v-image-input
        clearable
        :image-quality="0.90"
        imageMinScaling = "contain"
        v-model="fileData"
        @input="fileChange"
      />
  </div>
  <div class="mx-8">
   <v-row >
      <v-col>
        <t-input  :label="'No. de boleta'" :type="'String'" :rule="rule1"
        v-model="receiptNum"
        @input="numBoletaChange"
        ></t-input>
      </v-col>
    </v-row>
    <v-row >
      <v-col>
        <t-input  :label="'Banco de origen'" :type="'String'" :rule="rule1"
        v-model="bankName"
        @input="bancoChange"
        ></t-input>
      </v-col>
    </v-row>
  </div>

 </div>
</template>

<script>
import VImageInput from 'vuetify-image-input/a-la-carte';
import Input from "../Input.vue";
// import b64toBlob from 'b64-to-blob';
export default {
  data: () => ({
     rule1: [
         value => !!value || "Requerido",
      ],
  }),
 
  props: {
    // file_in: {},
    receiptNum: String,
    bankName: String,
    fileData: {}
  },
  methods: {
    numBoletaChange(){
      this.$emit('boletaWasChanged',this.receiptNum);
    },
    bancoChange(){
      this.$emit('bancoWasChanged',this.bankName);
    },
    fileChange(e){
      const files = e.target.files
      if(files[0] !== undefined) {
				this.$root.imageName = files[0].name
				if(this.$root.imageName.lastIndexOf('.') <= 0) {
					return
				}
				const fr = new FileReader ()
				fr.readAsDataURL(files[0])
				fr.addEventListener('load', () => {
					this.$root.imageUrl = fr.result
          this.$root.imageFile = files[0] // this is an image file that can be sent to server...
          this.$emit('fileWasChanged', this.$root.imageFile);
				})
			} else {
				this.$root.imageName = '';
				this.$root.imageFile = '';
				this.$root.imageUrl = '';
			}
      
    }
  },
  components:{
    'v-image-input': VImageInput,
    't-input': Input,
  }
   
};
</script>


<style scoped>
