<template>
  <div>
    <v-text-field 
      :label="label"
      :disabled="disabled"
      :rules="rule"
      :type="type"
      hide-details="auto"
      @input="onInputChange"
      v-model="inputValue"
      >
    </v-text-field>
  </div>
</template>

<script>
export default {
  data: function(){
    return{
      inputValue: this.value
    }
  },
  //props: ["label", "required", "placeholder", "type"],
  props: {
    value:{},
    label: {
      type: String,
      // adding more attributes to a property
      required: true,
      default: "No Label",
    },
    rule: {
      type: Array,
      // adding more attributes to a property
      required: true,
    },
    required: Boolean,
    type: String,
    disabled: Boolean,
  },
  methods: {
    onInputChange(){
      this.$emit('input',this.inputValue);
    }
  }
};
</script>


<style scoped>
/* Scoped styles are "component based" scope */
</style>>
